import React from 'react';
import ScrollAnimation from 'react-animate-on-scroll';
// import { Link } from 'react-router-dom';

const items = [
    {
        title: 'Coding Fun with Python Group courses',
        info: 'In this course bundle we will introduce Programming concepts with simple hands on fun Python projects. We start with simple drag and drop projects eventually progressing to learning coding using IDE.',
        numberOfCourses: '9 Courses',
        image: 'category-01_cropped.png',
        link: 'https://courses.m2mtechconnect.com/bundles/coding-fun-with-python-group-courses'
    },
    {
        title: 'Data Science - Data Analysis & Data Visualization courses',
        info: 'In this course bundle we will introduce Data Science concepts with simple hands on projects. We cover Data Analysis and Data Visualization concepts with simple hands on projects.',
        numberOfCourses: '12 Courses',
        image: 'category-02.webp',
        link: 'https://courses.m2mtechconnect.com/bundles/data-science-group-courses'
    },
    {
        title: 'Data Science - Advanced Visualizations & Intro to ML',
        info: 'In this course bundle we will go through Advanced Data Visualization projects and provide an Introduction to Machine Learning with simple hands on projects.',
        numberOfCourses: '9 Courses',
        image: 'category-02.webp',
        link: 'https://courses.m2mtechconnect.com/bundles/advanced-visualizations-intro-to-ml'
    },
    {
        title: 'Machine Learning & AI - Reinforcement Learning',
        info: 'In this course bundle we will go through Reinforcement Machine Learning hands on projects with simple hands on projects.',
        numberOfCourses: '9 Courses',
        image: 'category-02.webp',
        link: 'https://courses.m2mtechconnect.com/bundles/machine-learning-ai-part-1'
    }
];

const CategoryOne = ( { wrapperClass, styleClass, buttonStatus } ) => {
    return (
        <>
            <div className={ `row ${ wrapperClass || 'g-5 mt--25' }` }>
                { items.map( ( data , i ) => (
                    <ScrollAnimation 
                        animateIn="fadeInUp"
                        animateOut="fadeInOut"
                        className={ `col-lg-3 col-md-6 col-sm-6 col-12 ${ styleClass ? styleClass : '' }` }
                        animateOnce={ true }
                        key={ i }
                    >
                        <div className="service-card service-card-1 radius-small">
                            <div className="inner">
                                <div className="thumbnail">
                                    <a href={ data.link }>
                                        <img src={`${process.env.PUBLIC_URL}/images/category/category-01/${ data.image }`} alt="Category Thumb" loading="lazy" />
                                    </a>
                                </div>
                                <div className="content">
                                    <span className="course-total">{ data.numberOfCourses }</span>
                                    <h6 className="title"><a href={ data.link }>{ data.title }</a></h6>
                                    <p className="description">{ data.info }</p>
                                </div>
                            </div>
                        </div>
                    </ScrollAnimation>
                ) ) }
            </div>
            { buttonStatus !== 'disable' &&
                <div className="row text-center mt--60">
                    <div className="col-lg-12">
                        <ScrollAnimation 
                            animateIn="fadeInUp"
                            animateOut="fadeInOut"
                            className="load-more-btn"
                            animateOnce={ true }
                        >
                            <a className="edu-btn" href="https://courses.m2mtechconnect.com/collections">All Categories<i
                            className="icon-arrow-right-line-right"></i></a>
                        </ScrollAnimation>
                    </div>
                </div>
            }
    
        </>
    )
}

export default CategoryOne;