import React, { useEffect } from 'react';
import Checkbox from '../../../common/Checkbox';
import { transitionIn, transitionBack } from '../../../utils/transitions';

const EnrollmentFormInterest = ({
  reset,
  interestedInDataScience,
  setInterestedInDataScience,
  interestedInMachineLearning,
  setInterestedInMachineLearning,
  interestedInCoding,
  setInterestedInCoding,
  interestedInDesignThinking,
  setInterestedInDesignThinking,
  setShowEnrollmentFormGithub,
  setShowEnrollmentFormInterest,
  onSubmitInterest
  }) => {

  const transitionElementId = "FormQuestionInterest";

  const handleChangeDataScience = (e) => {
    setInterestedInDataScience(!interestedInDataScience);
  }

  const handleChangeMachineLearning = (e) => {
    setInterestedInMachineLearning(!interestedInMachineLearning);
  }

  const handleChangeCoding = (e) => {
    setInterestedInCoding(!interestedInCoding);
  }

  const handleChangeDesignThinking = (e) => {
    setInterestedInDesignThinking(!interestedInDesignThinking);
  }
  
  useEffect(() => {
    transitionIn(transitionElementId);
  }, [])

  return (
    <>
      <div className="enrollment-form-container">
        <div className="enrollment-form-wrapper">
          <h3 className="enrollment-form-head">Cohort Sign up<span className="enrollment-form-close-btn" onClick={() => {setShowEnrollmentFormInterest(false); reset()}}>&#x2715;</span></h3>
          <div className="enrollment-form-body">
            <div className="transition-in" id={transitionElementId}>
              <h4>Are you interested in</h4>
              <Checkbox
                label="Data Science"
                value={interestedInDataScience}
                onChange={handleChangeDataScience}
              />
              <Checkbox
                label="Machine Learning"
                value={interestedInMachineLearning}
                onChange={handleChangeMachineLearning}
              />
              <Checkbox
                label="Coding"
                value={interestedInCoding}
                onChange={handleChangeCoding}
              />
              <Checkbox
                label="Design Thinking"
                value={interestedInDesignThinking}
                onChange={handleChangeDesignThinking}
              />
            </div>
          </div>
          <div className="enrollment-form-footer">
            <button className='edu-btn' onClick={() => transitionBack( transitionElementId, setShowEnrollmentFormInterest, setShowEnrollmentFormGithub )}>Back</button>
            <button className='edu-btn' onClick={() => onSubmitInterest( transitionElementId )}>Next</button>
          </div>
        </div>
      </div>
    </>
  );
}

export default EnrollmentFormInterest;