import React, { useEffect } from 'react';
import { transitionIn } from '../../../utils/transitions';

const EnrollmentFormStepThankYou = ({ reset, setShowEnrollmentFormStepThankYou }) => {

  const transitionElementId = "FormThankYou";

  useEffect(() => {
    transitionIn(transitionElementId);
  }, [])

  return (
    <>
      <div className="enrollment-form-container">
        <div className="enrollment-form-wrapper">
          <h3 className="enrollment-form-head">Cohort Sign up<span className="enrollment-form-close-btn" onClick={() => {setShowEnrollmentFormStepThankYou(false); reset();}}>&#x2715;</span></h3>
          <div className="enrollment-form-body">
            <div className="transition-in" id={transitionElementId}>
              <h4>Thank you for your interest! We will contact you shortly.</h4>
              <p>Please send your resume as an email to <a href="mailto:contact@techbytesfoundation.org">contact@techbytesfoundation.org</a>.</p>
            </div>
          </div>
          <div className="enrollment-form-footer">
            <button className='edu-btn' style={{visibility: "hidden"}}>Back</button>
            <button className='edu-btn' onClick={() => setShowEnrollmentFormStepThankYou(false)}>Done</button>
          </div>
        </div>
      </div>
    </>
  );
}

export default EnrollmentFormStepThankYou;