import React, { useState, useEffect, useRef } from 'react';
import SEO from '../../common/SEO';
import HeaderTwo from '../../common/header/HeaderTwo';
import BannerOne from '../../components/banner/BannerOne';
import HomeOneAbout from '../../components/home-one/HomeOneAbout';
import HomeOneWhatWeDo from '../../components/home-one/HomeOneWhatWeDo';
import HomeOneHowWeDoIt from '../../components/home-one/HomeOneHowWeDoIt';
import HomeOneWhatYouWillLearn from '../../components/home-one/HomeOneWhatYouWillLearn';
import HomeOnePathsYouCanFollow from '../../components/home-one/HomeOnePathsYouCanFollow';
import HomeOneSuccessStory from '../../components/home-one/HomeOneSuccessStory';
import HomeOneCategory from '../../components/home-one/HomeOneCategory';
// import HomeOneCourses from '../../components/home-one/HomeOneCourses';
import TestimonialSectionFour from '../../components/testimonial-section/TestimonialSectionFour';
// import HomeOneInstructor from '../../components/home-one/HomeOneInstructor';
import VideoSectionTwo from '../../components/video-section/VideoSectionTwo';
import NewsLettterOne from '../../components/newsletter/NewsLettterOne';
// import HomeOneBlog from '../../components/home-one/HomeOneBlog';
import FooterOne from '../../common/footer/FooterOne';
import { transitionOut } from '../../utils/transitions';
import axios from 'axios';

const INITIAL_STATE = {
    formSubmitFrom: "https://www.m2mtechconnect.com",
    status: "",
    enrolledInUniversity: "",
    personalWebsite: "",
    interestedInDataScience: "",
    interestedInMachineLearning: "",
    interestedInCoding: "",
    interestedInDesignThinking: "",
    firstName: "",
    lastName: "",
    email: "",
};

const HomeOne = () => {

    const [status, setStatus] = useState("")
    const [enroll, setEnroll] = useState("")
    const [github, setGithub] = useState("")
    const [interestedInDataScience, setInterestedInDataScience] = useState("")
    const [interestedInMachineLearning, setInterestedInMachineLearning] = useState("")
    const [interestedInCoding, setInterestedInCoding] = useState("")
    const [interestedInDesignThinking, setInterestedInDesignThinking] = useState("")
    const [firstName, setFirstName] = useState("")
    const [lastName, setLastName] = useState("")
    const [email, setEmail] = useState("")

    const reset = () => {
        setStatus("");
        setEnroll("");
        setGithub("");
        setInterestedInDataScience("");
        setInterestedInMachineLearning("");
        setInterestedInCoding("");
        setInterestedInDesignThinking("");
        setFirstName("");
        setLastName("");
        setEmail("");
    }

    const [showEnrollmentFormStatus, setShowEnrollmentFormStatus] = useState(false)
    const [showEnrollmentFormEnroll, setShowEnrollmentFormEnroll] = useState(false)
    const [showEnrollmentFormGithub, setShowEnrollmentFormGithub] = useState(false)
    const [showEnrollmentFormInterest, setShowEnrollmentFormInterest] = useState(false)
    const [showEnrollmentFormUserInfo, setShowEnrollmentFormUserInfo] = useState(false)
    const [showEnrollmentFormStepThankYou, setShowEnrollmentFormStepThankYou] = useState(false)
    const [showEnrollmentFormStepEndingTwo, setShowEnrollmentFormStepEndingTwo] = useState(false)

    const [formSubmit, setFormSubmit] = useState(INITIAL_STATE);

    useEffect(() => {
        setFormSubmit(prevState => ({ ...prevState, "status": status }));
    }, [status])
    
    useEffect(() => {
        setFormSubmit(prevState => ({ ...prevState, "enrolledInUniversity": enroll }));
    }, [enroll])
    
    useEffect(() => {
        setFormSubmit(prevState => ({ ...prevState, "personalWebsite": github }));
    }, [github])
    
    useEffect(() => {
        setFormSubmit(prevState => ({ ...prevState, "interestedInDataScience": interestedInDataScience }));
    }, [interestedInDataScience])
    
    useEffect(() => {
        setFormSubmit(prevState => ({ ...prevState, "interestedInMachineLearning": interestedInMachineLearning }));
    }, [interestedInMachineLearning])
    
    useEffect(() => {
        setFormSubmit(prevState => ({ ...prevState, "interestedInCoding": interestedInCoding }));
    }, [interestedInCoding])
    
    useEffect(() => {
        setFormSubmit(prevState => ({ ...prevState, "interestedInDesignThinking": interestedInDesignThinking }));
    }, [interestedInDesignThinking])
    
    useEffect(() => {
        setFormSubmit(prevState => ({ ...prevState, "firstName": firstName }));
    }, [firstName])
    
    useEffect(() => {
        setFormSubmit(prevState => ({ ...prevState, "lastName": lastName }));
    }, [lastName])
    
    useEffect(() => {
        setFormSubmit(prevState => ({ ...prevState, "email": email }));
    }, [email])

    const handleFormSubmit = async () => {
        try {
            const url = `https://formspree.io/f/xlekeqld`;
            const { formSubmitFrom, status, enrolledInUniversity, personalWebsite, interestedInDataScience, interestedInMachineLearning, interestedInCoding, interestedInDesignThinking, firstName, lastName, email } = formSubmit;
            const payload = { formSubmitFrom, status, enrolledInUniversity, personalWebsite, interestedInDataScience, interestedInMachineLearning, interestedInCoding, interestedInDesignThinking, firstName, lastName, email };
            const response = await axios.post(url, payload);
            console.log(response);
            setFormSubmit(INITIAL_STATE);
        } catch (error) {
            console.log(error)
        }
    };

    const onSubmitStatus = (elementId) => {
        transitionOut(elementId);
        setTimeout(() => {
            setShowEnrollmentFormStatus(false);
            if(status === "None of the above") {
                setShowEnrollmentFormStepEndingTwo(true);
                setFormSubmit(INITIAL_STATE);
                reset();
            } else {
                setShowEnrollmentFormEnroll(true);
            }
        }, 300);
    }

    const onSubmitEnroll = (elementId) => {
        transitionOut(elementId);
        setTimeout(() => {
            setShowEnrollmentFormEnroll(false);
            if(enroll === "No") {
                setShowEnrollmentFormStepEndingTwo(true);
                setFormSubmit(INITIAL_STATE);
                reset();
            } else {
                setShowEnrollmentFormGithub(true);
            }
        }, 300);
    }

    const onSubmitGithub = (elementId) => {
        transitionOut(elementId);
        setTimeout(() => {
            setShowEnrollmentFormGithub(false);
            setShowEnrollmentFormInterest(true);
        }, 300);
    }

    const onSubmitInterest = (elementId) => {
        transitionOut(elementId);
        setTimeout(() => {
            setShowEnrollmentFormInterest(false);
            setShowEnrollmentFormUserInfo(true);
        }, 300);
    }
    
    const onSubmitUserInfo = (elementId) => {
        transitionOut(elementId);
        setTimeout(() => {
            setShowEnrollmentFormUserInfo(false);
            setShowEnrollmentFormStepThankYou(true);
            handleFormSubmit();
            reset();
        }, 300);
    }

    const GoogleReviews = useRef();

    return (
        <>
            <SEO title="M2M Tech Connect" />

            <HeaderTwo styles="header-transparent header-style-2" searchDisable />

            <BannerOne
            reset={reset}
            status={status}
            setStatus={setStatus}
            showEnrollmentFormStatus={showEnrollmentFormStatus}
            setShowEnrollmentFormStatus={setShowEnrollmentFormStatus}
            onSubmitStatus={onSubmitStatus}
            enroll={enroll}
            setEnroll={setEnroll}
            showEnrollmentFormEnroll={showEnrollmentFormEnroll}
            setShowEnrollmentFormEnroll={setShowEnrollmentFormEnroll}
            onSubmitEnroll={onSubmitEnroll}
            github={github}
            setGithub={setGithub}
            showEnrollmentFormGithub={showEnrollmentFormGithub}
            setShowEnrollmentFormGithub={setShowEnrollmentFormGithub}
            onSubmitGithub={onSubmitGithub}
            interestedInDataScience={interestedInDataScience}
            setInterestedInDataScience={setInterestedInDataScience}
            interestedInMachineLearning={interestedInMachineLearning}
            setInterestedInMachineLearning={setInterestedInMachineLearning}
            interestedInCoding={interestedInCoding}
            setInterestedInCoding={setInterestedInCoding}
            interestedInDesignThinking={interestedInDesignThinking}
            setInterestedInDesignThinking={setInterestedInDesignThinking}
            showEnrollmentFormInterest={showEnrollmentFormInterest}
            setShowEnrollmentFormInterest={setShowEnrollmentFormInterest}
            onSubmitInterest={onSubmitInterest}
            firstName={firstName}
            setFirstName={setFirstName}
            lastName={lastName}
            setLastName={setLastName}
            email={email}
            setEmail={setEmail}
            showEnrollmentFormUserInfo={showEnrollmentFormUserInfo}
            setShowEnrollmentFormUserInfo={setShowEnrollmentFormUserInfo}
            onSubmitUserInfo={onSubmitUserInfo}
            showEnrollmentFormStepThankYou={showEnrollmentFormStepThankYou}
            setShowEnrollmentFormStepThankYou={setShowEnrollmentFormStepThankYou}
            showEnrollmentFormStepEndingTwo={showEnrollmentFormStepEndingTwo}
            setShowEnrollmentFormStepEndingTwo={setShowEnrollmentFormStepEndingTwo}
            GoogleReviews={GoogleReviews}
            />
            
            <HomeOneAbout />

            <HomeOneWhatWeDo />

            <HomeOneHowWeDoIt />

            <HomeOneWhatYouWillLearn />

            <HomeOnePathsYouCanFollow />

            <HomeOneCategory />

            {/* <HomeOneCourses /> */}

            <VideoSectionTwo />

            <TestimonialSectionFour GoogleReviews={GoogleReviews} />

            <HomeOneSuccessStory />

            {/* <HomeOneInstructor /> */}

            <NewsLettterOne />

            {/* <HomeOneBlog /> */}

            <FooterOne />   
        </>
    )
}

export default HomeOne;