import React, { useState, useEffect } from 'react';
import { transitionIn, transitionBack } from '../../../utils/transitions';

const EnrollmentFormEnroll = ({ reset, enroll, setEnroll, setShowEnrollmentFormStatus, setShowEnrollmentFormEnroll, onSubmitEnroll }) => {

  const [disabled, setDisabled] = useState(true);

  const transitionElementId = "FormQuestionEnroll";

  const onOptionChange = (e) => {
    setEnroll(e.target.value);
  }
    
  useEffect(() => {
    if( enroll !== "") {
      setDisabled(false);
    }
  }, [enroll])

  useEffect(() => {
    transitionIn(transitionElementId);
  }, [])

  return (
    <>
      <div className="enrollment-form-container">
        <div className="enrollment-form-wrapper">
          <h3 className="enrollment-form-head">Cohort Sign up<span className="enrollment-form-close-btn" onClick={() => {setShowEnrollmentFormEnroll(false); reset()}}>&#x2715;</span></h3>
          <div className="enrollment-form-body">
            <div className="transition-in" id={transitionElementId}>
              <h4>Are you currently enrolled in any Canadian University?</h4>
              <input
                type="radio"
                value="Yes"
                name="enroll"
                id="q3o1"
                checked={enroll === "Yes"}
                onChange={onOptionChange}
              />
              <label htmlFor="q3o1">&nbsp;Yes</label><br></br>
              <input
                type="radio"
                value="No"
                name="enroll"
                id="q3o2"
                checked={enroll === "No"}
                onChange={onOptionChange}
              />
              <label htmlFor="q3o2">&nbsp;No</label><br></br>
            </div>
          </div>
          <div className="enrollment-form-footer">
            <button className='edu-btn' onClick={() => transitionBack( transitionElementId, setShowEnrollmentFormEnroll, setShowEnrollmentFormStatus )}>Back</button>
            <button className='edu-btn' disabled={disabled} onClick={() => onSubmitEnroll(transitionElementId, enroll )}>Next</button>
          </div>
        </div>
      </div>
    </>
  );
}

export default EnrollmentFormEnroll;