import React, { useEffect } from 'react';
import { transitionIn } from '../../../utils/transitions';

const EnrollmentFormStepEndingTwo = ({ reset, setShowEnrollmentFormStepEndingTwo }) => {

  const transitionElementId = "FormEndingTwo";

  useEffect(() => {
    transitionIn(transitionElementId);
  }, [])

  return (
    <>
      <div className="enrollment-form-container">
        <div className="enrollment-form-wrapper">
          <h3 className="enrollment-form-head">Cohort Sign up<span className="enrollment-form-close-btn" onClick={() => {setShowEnrollmentFormStepEndingTwo(false); reset();}}>&#x2715;</span></h3>
          <div className="enrollment-form-body">
            <div className="transition-in" id={transitionElementId}>
              <h4>Sorry! Unfortunately, you are not eligible for internships with us.</h4>
              <p>Feel free to enroll again if your status changes.</p>
            </div>
          </div>
          <div className="enrollment-form-footer">
            <button className='edu-btn' style={{visibility: "hidden"}}>Back</button>
            <button className='edu-btn' onClick={() => setShowEnrollmentFormStepEndingTwo(false)}>Done</button>
          </div>
        </div>
      </div>
    </>
  );
}

export default EnrollmentFormStepEndingTwo;