import React, { useState, useEffect } from 'react';
import { transitionIn } from '../../../utils/transitions';

const EnrollmentFormStatus = ({ reset, status, setStatus, setShowEnrollmentFormStatus, onSubmitStatus }) => {

  const [disabled, setDisabled] = useState(true);

  const transitionElementId = "FormQuestionStatus";

  const onOptionChange = (e) => {
    setStatus(e.target.value);
  }

  useEffect(() => {
    if( status !== "") {
      setDisabled(false);
    }
  }, [status])

  useEffect(() => {
    transitionIn(transitionElementId);
  }, [])

  return (
    <>
      <div className="enrollment-form-container">
        <div className="enrollment-form-wrapper">
          <h3 className="enrollment-form-head">Cohort Sign up<span className="enrollment-form-close-btn" onClick={() => {setShowEnrollmentFormStatus(false); reset()}}>&#x2715;</span></h3>
          <div className="enrollment-form-body">
            <div className="transition-in" id={transitionElementId}>
              <h4>Are you a Canadian Permanent Resident (PR), Canadian Citizen or International Student?</h4>
              <input
                type="radio"
                value="Canadian Permanent Resident"
                name="status"
                id="q1o1"
                checked={status === "Canadian Permanent Resident"}
                onChange={onOptionChange}
              />
              <label htmlFor="q1o1">&nbsp;Canadian Permanent Resident</label><br></br>
              <input
                type="radio"
                value="Canadian Citizen"
                name="status"
                id="q1o2"
                checked={status === "Canadian Citizen"}
                onChange={onOptionChange}
              />
              <label htmlFor="q1o2">&nbsp;Canadian Citizen</label><br></br>
              <input
                type="radio"
                value="International Student"
                name="status"
                id="q1o3"
                checked={status === "International Student"}
                onChange={onOptionChange}
              />
              <label htmlFor="q1o3">&nbsp;International Student</label><br></br>
              <input
                type="radio"
                value="None of the above"
                name="status"
                id="q1o4"
                checked={status === "None of the above"}
                onChange={onOptionChange}
              />
              <label htmlFor="q1o4">&nbsp;None of the above</label><br></br>
            </div>
          </div>
          <div className="enrollment-form-footer">
          <button className='edu-btn' style={{visibility: 'hidden'}}>Back</button>
            <button className='edu-btn' disabled={disabled} onClick={() => onSubmitStatus( transitionElementId )}>Next</button>
          </div>
        </div>
      </div>
    </>
  );
}

export default EnrollmentFormStatus;