import React, { useState, useEffect } from 'react';
import validator from 'validator';
import { transitionIn, transitionBack } from '../../../utils/transitions';

const EnrollmentFormUserInfo = ({ reset, firstName, setFirstName, lastName, setLastName, email, setEmail, setShowEnrollmentFormInterest, setShowEnrollmentFormUserInfo, onSubmitUserInfo }) => {

  const [invalidEmailMessage, setInvalidEmailMessage] = useState("")
  const [disabled, setDisabled] = useState(true)

  const transitionElementId = "FormQuestionUserInfo";

  const validateEmail = (email) => {
    if(email !== "") {
      if (validator.isEmail(email)) {
        setInvalidEmailMessage("");
        setDisabled(false);
      } else {
        setInvalidEmailMessage("Is not a valid Email!");
        setDisabled(true)
      }
    } else if(email === "") {
      setInvalidEmailMessage("");
      setDisabled(true);
    }
  };

  const handleFirstNameChange = (e) => {
    setFirstName(e.target.value)
  }
 
  const handleLastNameChange = (e) => {
    setLastName(e.target.value)
  }
    
  const handleEmailChange = (e) => {
    setEmail(e.target.value)
  }
        
  useEffect(() => {
      validateEmail(email);
  }, [email])
 
  useEffect(() => {
    transitionIn(transitionElementId);
  }, [])

  return (
    <>
      <div className="enrollment-form-container">
        <div className="enrollment-form-wrapper">
          <h3 className="enrollment-form-head">Cohort Sign up<span className="enrollment-form-close-btn" onClick={() => {setShowEnrollmentFormUserInfo(false); reset()}}>&#x2715;</span></h3>
          <div className="enrollment-form-body">
            <div className="transition-in" id={transitionElementId}>
              <h4>Please share your contact information</h4>
              <label>First Name<br></br>
                <input
                  type="text"
                  value={firstName}
                  placeholder="John..."
                  name="first name"
                  id="q7o1"
                  className="enrollment-form-link-input"
                  onChange={handleFirstNameChange}
                />
              </label><br></br>
              <label className="mt-10">Last Name<br></br>
                <input
                  type="text"
                  value={lastName}
                  placeholder="Smith..."
                  name="last name"
                  id="q7o2"
                  className="enrollment-form-link-input"
                  onChange={handleLastNameChange}
                />
              </label><br></br>
              <label className="mt-10">Email<br></br>
                <input
                  type="text"
                  value={email}
                  placeholder="name@example.com"
                  name="email"
                  id="q7o3"
                  className="enrollment-form-link-input"
                  onChange={(e) => handleEmailChange(e)}
                />
                <br></br>
                <span style={{ fontWeight: "bold", color: "red" }}>{invalidEmailMessage}</span>
              </label>
            </div>
          </div>
          <div className="enrollment-form-footer">
            <button className='edu-btn' onClick={() => transitionBack( transitionElementId, setShowEnrollmentFormUserInfo, setShowEnrollmentFormInterest )}>Back</button>
            <button className='edu-btn' disabled={disabled} onClick={() => onSubmitUserInfo( transitionElementId )}>Next</button>
          </div>
        </div>
      </div>
    </>
  );
}

export default EnrollmentFormUserInfo;