import React, { useState, useEffect } from 'react';
import validator from 'validator';
import { transitionIn, transitionBack } from '../../../utils/transitions';

const EnrollmentFormGithub = ({ reset, github, setGithub, setShowEnrollmentFormEnroll, setShowEnrollmentFormGithub, onSubmitGithub }) => {
  
  const [invalidURLMessage, setInvalidURLMessage] = useState("")
  const [disabled, setDisabled] = useState(true);

  const transitionElementId = "FormQuestionGithub";

  const validateLink = (link) => {
    if(link !== "") {
      if (validator.isURL(link)) {
        setInvalidURLMessage("");
        setDisabled(false);
      } else {
        setInvalidURLMessage("Is not a valid link.");
        setDisabled(true);
      }
    } else if(link === "") {
      setInvalidURLMessage("");
      setDisabled(true);
    }
  }

  const onTextChange = (e) => {
    setGithub(e.target.value);
  }
      
  useEffect(() => {
    validateLink(github)
  }, [github])

  useEffect(() => {
    transitionIn(transitionElementId);
  }, [])

  return (
    <>
      <div className="enrollment-form-container">
        <div className="enrollment-form-wrapper">
          <h3 className="enrollment-form-head">Cohort Sign up<span className="enrollment-form-close-btn" onClick={() => {setShowEnrollmentFormGithub(false); reset()}}>&#x2715;</span></h3>
            <div className="enrollment-form-body">
              <div className="transition-in" id={transitionElementId}>
                <h4>Please provide your GitHub or personal website link which describes your STEM skills.</h4>
                <br></br>
                <div>
                  <input type="text" name="github" id="q5" value={github} className="enrollment-form-link-input" onChange={(e) => onTextChange(e)} />
                  <br></br>
                  <span style={{ fontWeight: 'bold', color: 'red' }}>{invalidURLMessage}</span>
                </div>
              </div>
            </div>
            <div className="enrollment-form-footer">
              <button className='edu-btn' onClick={() => transitionBack( transitionElementId, setShowEnrollmentFormGithub, setShowEnrollmentFormEnroll )}>Back</button>
              <button className='edu-btn' disabled={disabled} onClick={() => onSubmitGithub( transitionElementId )}>Next</button>
            </div>
        </div>
      </div>
    </>
  );
}

export default EnrollmentFormGithub;